<template>
  <div id="nav">
    <div class="profile">
      <div class="photo" :style="{ background: `url(${user.meta ? user.meta.photo : require('../../assets/images/icon/user.svg')})`}"></div>
      <div class="content">
        <h1>{{ user.username }}</h1>
        <p class="tag">{{ role[user.role] }}</p>
      </div>
    </div>
    <div class="menu">
      <ul>
        <li><router-link to="/posts/new"></router-link></li>
        <li><router-link to="/posts"></router-link></li>
      </ul>
    </div>
    <div class="menu">
      <ul>
        <li><router-link to="/stock/new"></router-link></li>
        <li><router-link to="/stock"></router-link></li>
      </ul>
    </div>
    <div class="menu">
      <ul>
        <li><router-link :to="{ name: 'CreateCoupon', force: true, query: { type: 1 } }"></router-link></li>
        <li><router-link :to="{ name: 'CouponList', force: true }"></router-link></li>
      </ul>
    </div>
    <div class="menu">
      <ul>
        <li><router-link :to="{ name: 'TransactionList', force: true }"></router-link></li>
      </ul>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    data () {
      return {
        role: ['', '', '', '', 'superuser']
      }
    },
    computed: {
      ...mapGetters([ 'user' ])
    }
  }
</script>



